/* ====== UNITY ====== */
.unity-page {
  display: flex;
  justify-content: center;
  padding: 20px 16px;
}

.unity-card {
  max-width: 1000px;
  width: 100%;
  background: var(--b1);
  padding: 10px;
  border-radius: 20px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
}

.unity-container {
  width: 100%;
  /* max-width: 960px; —à–∏—Ä–∏–Ω–∞ –∏–≥—Ä—ã –ø–æ —Å—Ç–∏–ª—é canvas */
  margin: 0 auto;
  background: var(--b1);
  border-radius: 20px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
  overflow: hidden;
}

.unity-scaler {
  position: relative;
  width: 100%;
  padding: 660px 0px 0px 0px;  /*–ø—Ä–æ–ø–æ—Ä—Ü–∏–∏ canvas: 600px / 960px */

}

.unity-frame {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}


.unity-note {
  margin-top: 12px;
  font-size: 14px;
  color: var(--tx2);
  text-align: center;
}

/* ============================
   üì± MOBILE ADAPTATION
   ============================ */
@media (max-width: 768px) {

  .unity-page {
    padding: 12px 10px;
  }

  .unity-card {
    padding: 12px;
    border-radius: 16px;
  }

  .unity-container {
    border-radius: 16px;
  }

  /* –ú–∞—Å—à—Ç–∞–±–∏—Ä—É–µ–º canvas –ø–æ–¥ –º–æ–±–∏–ª—å–Ω—ã–π —ç–∫—Ä–∞–Ω */
  .unity-scaler {
    padding-top: 62%; /* –≤–º–µ—Å—Ç–æ —Ñ–∏–∫—Å–∏—Ä–æ–≤–∞–Ω–Ω—ã—Ö 660px ‚Äî –∞–¥–∞–ø—Ç–∏–≤–Ω–æ–µ —Å–æ–æ—Ç–Ω–æ—à–µ–Ω–∏–µ */
  }

  .unity-note {
    font-size: 13px;
    margin-top: 10px;
    padding: 0 6px;
    line-height: 1.45;
  }
}

/* ============================
   üì± VERY SMALL SCREENS (0‚Äì480px)
   ============================ */
@media (max-width: 480px) {

  .unity-card {
    padding: 10px;
  }

  .unity-scaler {
    padding-top: 70%; /* —á—É—Ç—å –≤—ã—à–µ, —á—Ç–æ–±—ã –∏–≥—Ä–∞ –Ω–µ –±—ã–ª–∞ —Å–ª–∏—à–∫–æ–º —É–∑–∫–æ–π */
  }

  .unity-note {
    font-size: 12px;
  }
}
