/* ====== DESIGN ====== */
.design-block {
  background: var(--b1);
  width: 960px;
  height: auto;
  border-radius: 20px;
  padding: 20px;
  margin: 0px auto;
  display: flex; 
  flex-direction: column; 
  align-items: center;
}

.image-viewer {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  padding: 20px;
}

/* –û–¥–Ω–æ –±–æ–ª—å—à–æ–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ */
.viewer-image {
  max-height: 320px;
  height: 100%;
  border-radius: 12px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
  transition: opacity 0.4s ease;
  justify-content: center;
}

/* –¢—Ä–∏ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤ —Ä—è–¥ */
.gallery-row {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.gallery-image {
  height: 420px;
  border-radius: 12px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
  transition: opacity 0.4s ease;
}

.duo-row {
  display: flex;
  gap: 20px;
  justify-content: center;
  flex-wrap: wrap;
}

.duo-image {
  height: 320px;
  object-fit: cover;
  border-radius: 12px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
  transition: opacity 0.4s ease;
}

.s7 {
  max-width: 940px;
  width: 100%;
  border-radius: 6px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
  transition: opacity 0.4s ease;
  justify-content: center;
}
.s7:first-child { margin-bottom: 20px; }

.v3d {
  max-width: 940px;
  width: 100%;
  border-radius: 6px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
  transition: opacity 0.4s ease;
  justify-content: center;
}

/* –ê–Ω–∏–º–∞—Ü–∏—è –ø–æ—è–≤–ª–µ–Ω–∏—è */
.fade-in {
  opacity: 0;
  animation: fadeIn 0.4s forwards;
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

/* –ú–∏–Ω–∏–∞—Ç—é—Ä—ã + —Å—Ç—Ä–µ–ª–∫–∏ */
.thumb-wrapper {
  display: flex;
  align-items: center;
  gap: 12px;
  padding-top: 20px;
}

.thumb-btn {
  background: var(--b1);
  border: 1px solid var(--b2);
  padding: 8px 12px;
  border-radius: 10px;
  cursor: pointer;
  font-size: 18px;
  transition: 0.2s;
}

.thumb-btn:hover {
  background: var(--b2);
}

.thumbnail-row {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  justify-content: center;
}

.thumbnail {
  width: 80px;
  height: 60px;
  object-fit: cover;
  border-radius: 6px;
  cursor: pointer;
  opacity: 0.6;
  transition: 0.2s;
  border: 2px solid transparent;
}

.thumbnail:hover {
  opacity: 1;
}

.thumbnail.active {
  opacity: 1;
  border-color: var(--b2);
}

/* ============================
   üì± MOBILE ADAPTATION
   ============================ */
@media (max-width: 768px) {

  /* –û—Å–Ω–æ–≤–Ω–æ–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä */
  .design-block {
    width: 100%;
    padding: 16px;
    border-radius: 16px;
  }

  .image-viewer {
    padding: 12px;
    gap: 16px;
  }

  /* –ë–æ–ª—å—à–æ–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ */
  .viewer-image {
    max-height: none;
    width: 100%;
    height: auto;
    max-width: 480px;
  }

  /* –¢—Ä–∏ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤ —Ä—è–¥ ‚Üí –≤ –∫–æ–ª–æ–Ω–∫—É */
  .gallery-row {
    flex-direction: column;
    gap: 14px;
  }

  .gallery-image {
    width: 100%;
    height: auto;
    max-width: 480px;
  }

  /* –î–≤–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è –≤ —Ä—è–¥ ‚Üí –≤ –∫–æ–ª–æ–Ω–∫—É */
  .duo-row {
    flex-direction: column;
    gap: 14px;
  }

  .duo-image {
    width: 100%;
    height: auto;
    max-width: 480px;
  }

  /* –ë–æ–ª—å—à–∏–µ —à–∏—Ä–æ–∫–∏–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è */
  .s7,
  .v3d {
    width: 100%;
    max-width: 480px;
  }

  /* –ú–∏–Ω–∏–∞—Ç—é—Ä—ã + —Å—Ç—Ä–µ–ª–∫–∏ */
  .thumb-wrapper {
    gap: 10px;
    padding-top: 14px;
    flex-wrap: wrap;
    justify-content: center;
  }

  .thumb-btn {
    padding: 6px 10px;
    font-size: 16px;
  }

  .thumbnail-row {
    gap: 8px;
  }

  .thumbnail {
    width: 70px;
    height: 52px;
  }
}

/* ============================
   üì± VERY SMALL SCREENS (0‚Äì480px)
   ============================ */
@media (max-width: 480px) {

  .design-block {
    padding: 12px;
  }

  .viewer-image,
  .gallery-image,
  .duo-image,
  .s7,
  .v3d {
    max-width: 360px;
  }

  .thumbnail {
    width: 60px;
    height: 45px;
  }

  .thumb-btn {
    font-size: 14px;
    padding: 5px 8px;
  }
}
