/* –°—Ç—Ä–∞–Ω–∏—Ü–∞ */
.foto-page {
  display: flex;
  justify-content: center;
  padding: 20px 0;
}

.foto-block {
  background: var(--b1);
  width: 960px;
  border-radius: 20px;
  padding: 30px 20px;
  box-shadow: var(--sh);
}

/* –°–µ—Ç–∫–∞ –æ—Å—Ç–∞–ª—å–Ω—ã—Ö —Ñ–æ—Ç–æ */
.foto-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.foto-item {
  width: 220px;
  height: 160px;
  object-fit: cover;
  border-radius: 6px;
  border: 1px solid var(--b2);
  transition: 0.3s ease;
}

.foto-item:hover {
  transform: scale(1.03);
}

/* –û–±—â–∏–π –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–∞—Å—Ç–æ–º–Ω—ã—Ö –±–ª–æ–∫–æ–≤ */
.layout,
.layout2 {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

/* –û–±—â–∏–µ —Å—Ç–∏–ª–∏ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π */
.img {
  object-fit: cover;
  border-radius: 6px;
  border: 1px solid var(--b2);
  box-sizing: border-box; 
  display: block;
}

.img:hover{
  transform: scale(1.04);
  box-shadow: 0 14px 32px rgba(0,0,0,0.25);
}

/* --- –ë–õ–û–ö 1 (—Ñ–æ—Ç–æ 1‚Äì6) --- */

/* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ */
.tall {
  width: 292px;
  height: 392px;
}

/* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–µ –≤–µ—Ä—Ö–Ω–∏–µ */
.h1 {
  width: 302px;
  height: 227px; /* 4:3 */
  margin-bottom: 20px;
}

/* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–µ –Ω–∏–∂–Ω–∏–µ */
.h2 {
  width: 194px;
  height: 146px; /* 4:3 */
}

/* --- –ë–õ–û–ö 2 (—Ñ–æ—Ç–æ 7‚Äì11) --- */

.left2 {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.row {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  justify-content: center;
  align-items: center;
}

/* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–µ 4:3 */
.h3 {
    width: 278px;
    height: 210px; /* 4:3 */
    margin-bottom: 20px;
}

/* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ —Å–ø—Ä–∞–≤–∞ */
.v2 {
  width: 342px;
  height: 442px;
}

/* --- –ë–õ–û–ö 3 (—Ñ–æ—Ç–æ 12‚Äì23) --- */
.layout3 {
  display: flex;
  flex-direction: column; /* ‚Üê –∫–ª—é—á–µ–≤–æ–π –º–æ–º–µ–Ω—Ç */
  gap: 20px;
  margin-bottom: 20px;
  margin-top: -20px;
}

/* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–µ 4:3 */
.h4 {
  width: 388px;
  height: 313px;
}

.h5 {
  width: 300px;
  height: 225px;
}

/* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω—ã–µ 3:4 ‚Äî –∞–¥–∞–ø—Ç–∏—Ä–æ–≤–∞–Ω—ã –ø–æ–¥ –≤—ã—Å–æ—Ç—É 225px */
.v3 {
  width: 256px;
  height: 313px;
}

.v4 {
  width: 300px;
  height: 357px;
}

.layout4 {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 2px;
}

.layout4 .col {
  display: flex;
  flex-direction: column;
  gap: 0px;
  align-items: center;
}

/* –ë–ª–æ–∫ 5 ‚Äî —Ç—Ä–∏ —Å—Ç–æ–ª–±–∏–∫–∞ */
.layout5 {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.col5 {
  width: 300px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  align-items: center;
}

/* –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–µ (3:4) */
.v5 {
  width: 300px;
  height: 400px;
}

/* –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–µ (4:3) */
.h5 {
  width: 300px;
  height: 225px;
}

.layout6 {
  display: flex;
  flex-direction: column; /* ‚Üê –∫–ª—é—á–µ–≤–æ–π –º–æ–º–µ–Ω—Ç */
  gap: 20px;
}

.viewer {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  cursor: zoom-out;
}

.viewer-img {
  max-width: 90%;
  max-height: 90%;
  border-radius: 10px;
  box-shadow: 0 0 40px rgba(0,0,0,0.5);
  cursor: default;
}

.viewer-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  font-size: 42px;
  background: rgba(255,255,255,0.2);
  border: none;
  padding: 10px 18px;
  border-radius: 10px;
  cursor: pointer;
  color: white;
  transition: 0.2s;
}

.viewer-btn:hover {
  background: rgba(255,255,255,0.35);
}

.viewer-btn.left {
  left: 40px;
}

.viewer-btn.right {
  right: 40px;
}

/* ============================
   üì± MOBILE ADAPTATION
   ============================ */
@media (max-width: 768px) {

  /* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä —Å—Ç—Ä–∞–Ω–∏—Ü—ã */
  .foto-page {
    padding: 10px 0;
  }

  .foto-block {
    width: 100%;
    padding: 20px 12px;
    border-radius: 14px;
  }

  /* –°–µ—Ç–∫–∞ –æ—Å—Ç–∞–ª—å–Ω—ã—Ö —Ñ–æ—Ç–æ */
  .foto-grid {
    gap: 12px;
  }

  .foto-item {
    width: 46%;
    height: auto;
    aspect-ratio: 4 / 3;
  }

  /* –í—Å–µ –∫–∞—Å—Ç–æ–º–Ω—ã–µ –±–ª–æ–∫–∏ –ø—Ä–µ–≤—Ä–∞—â–∞–µ–º –≤ –∫–æ–ª–æ–Ω–∫—É */
  .layout,
  .layout2,
  .layout3,
  .layout4,
  .layout5,
  .layout6 {
    flex-direction: column;
    align-items: center;
    gap: 14px;
  }

  /* –í—Å–µ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è —Å—Ç–∞–Ω–æ–≤—è—Ç—Å—è —Ä–µ–∑–∏–Ω–æ–≤—ã–º–∏ */
  .img,
  .tall,
  .h1,
  .h2,
  .h3,
  .h4,
  .h5,
  .v2,
  .v3,
  .v4,
  .v5 {
    width: 100% !important;
    height: auto !important;
    max-width: 420px;
  }

  /* –°—Ç–æ–ª–±—Ü—ã —Ç–æ–∂–µ –≤ –∫–æ–ª–æ–Ω–∫—É */
  .left2,
  .col,
  .col5 {
    width: 100%;
    align-items: center;
  }

  .row {
    flex-wrap: wrap;
    gap: 14px;
  }

  /* Viewer –∞–¥–∞–ø—Ç–∞—Ü–∏—è */
  .viewer-img {
    max-width: 95%;
    max-height: 80%;
  }

  .viewer-btn {
    font-size: 28px;
    padding: 8px 14px;
  }

  .viewer-btn.left {
    left: 10px;
  }

  .viewer-btn.right {
    right: 10px;
  }
}
