/* ====== VIDEO ====== */
.video-page {
  display: flex;
  justify-content: center;
  padding: 20px 16px;
}

.video-card {
  max-width: 1000px;
  width: 100%;
  background: var(--b1);
  padding: 20px;
  border-radius: 20px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
  text-align: center;
}

.video-grid {
  display: flex;
  flex-direction: column;
  gap: 24px;
  align-items: center;
}

.video-item h2 {
  margin-top: 0px;
  margin-bottom: 20px;
  font-size: 18px;
}

.video-player {
  width: 60%;
  border-radius: 12px;
  border: 1px solid var(--b2);
  display: block;
  margin: 0 auto;
}

.video-desc {
  margin-top: 20px;
  font-size: 14px;
  color: var(--tx2);
  margin-bottom: 20px;
  padding-bottom: 20px;
  border-bottom: 2px solid var(--b2);
}

.video-desc:last-child { 
  border-bottom: none; 
  padding-bottom: 0; 
}

/* ============================
   üì± MOBILE ADAPTATION
   ============================ */
@media (max-width: 768px) {

  .video-page {
    padding: 12px 10px;
  }

  .video-card {
    padding: 16px;
    border-radius: 16px;
  }

  .video-item h2 {
    font-size: 16px;
    margin-bottom: 14px;
    padding: 0 6px;
  }

  /* –í–∏–¥–µ–æ —Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è —Ä–µ–∑–∏–Ω–æ–≤—ã–º */
  .video-player {
    width: 100%;
    max-width: 480px;
    border-radius: 10px;
  }

  .video-desc {
    font-size: 13px;
    margin-top: 14px;
    margin-bottom: 14px;
    padding-bottom: 14px;
  }
}

/* ============================
   üì± VERY SMALL SCREENS (0‚Äì480px)
   ============================ */
@media (max-width: 480px) {

  .video-card {
    padding: 14px 10px;
  }

  .video-item h2 {
    font-size: 15px;
  }

  .video-player {
    width: 100%;
    max-width: 360px;
  }

  .video-desc {
    font-size: 12px;
    line-height: 1.45;
  }
}
