/* ====== RESUME ====== */
.resume-header {
  margin-bottom: 40px;
}

.resume-photo {
  float: right;
  width: 120px;
  max-width: 40%;
  aspect-ratio: 13 / 17;
  object-fit: cover;
  border-radius: 16px;
  border: 2px solid var(--b2);
  margin: -20px 0 0px 0px; /* –æ—Ç—Å—Ç—É–ø—ã –æ—Ç —Ç–µ–∫—Å—Ç–∞ */
}

.resume-subtitle {
  margin-top: 4px;
  font-size: 14px;
  color: var(--tx2);
  padding-right: 160px;
}

.skills-list li {
  margin-bottom: 6px;
  line-height: 1.4;
}

.resume-page {
  display: flex;
  justify-content: center;
  padding: 20px 16px;
}

.resume-card {
  max-width: 800px;
  width: 100%;
  background: var(--b1);
  padding: 30px;
  border-radius: 20px;
  border: 1px solid var(--b2);
  box-shadow: var(--sh);
  position: relative;
}

.resume-card h1 {
  margin-bottom: 20px;
  font-size: 28px;
}

.resume-block {
  margin-bottom: 20px;
  padding-bottom: 20px; 
  border-bottom: 1px solid var(--b2);
}

.resume-block h2 {
  margin-bottom: 8px;
  font-size: 20px;
}

.resume-block ul {
  padding-left: 18px;
}

.resume-block:last-child { 
  border-bottom: none; 
  padding-bottom: 0; 
}

/* ============================
   üì± MOBILE ADAPTATION
   ============================ */
@media (max-width: 768px) {

  .resume-page {
    padding: 14px 10px;
  }

  .resume-card {
    padding: 20px;
    border-radius: 16px;
  }

  /* –§–æ—Ç–æ –ø–µ—Ä–µ—Å—Ç–∞—ë—Ç float'–∏—Ç—å—Å—è –∏ —Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è —Å–≤–µ—Ä—Ö—É */
  .resume-photo {
    float: none;
    display: block;
    margin: 0 auto 16px auto;
    width: 140px;
    max-width: 60%;
  }

  .resume-header {
    text-align: center;
    margin-bottom: 24px;
  }

  .resume-card h1 {
    font-size: 24px;
    margin-bottom: 14px;
  }

  .resume-subtitle {
    padding-right: 0;
    font-size: 13px;
    margin-top: 6px;
    text-align: center;
  }

  .resume-block {
    margin-bottom: 16px;
    padding-bottom: 16px;
  }

  .resume-block h2 {
    font-size: 18px;
  }

  .resume-block ul {
    padding-left: 16px;
  }

  .skills-list li {
    margin-bottom: 5px;
    font-size: 14px;
  }
}

@media (max-width: 480px) {

  .resume-card {
    padding: 16px;
  }

  .resume-photo {
    width: 120px;
    max-width: 70%;
  }

  .resume-card h1 {
    font-size: 22px;
  }

  .resume-block h2 {
    font-size: 17px;
  }

  .resume-subtitle {
    font-size: 12px;
  }

  .skills-list li {
    font-size: 13px;
  }
}
